import {
    Container,
    SimpleGrid,
    Image,
    Flex,
    Heading,
    Text,
    Stack,
    StackDivider,
    Icon,
    useColorModeValue,
} from '@chakra-ui/react';
import {
    IoAnalyticsSharp,
    IoLogoBitcoin,
    IoSearchSharp,
} from 'react-icons/io5';
//   import { ReactElement } from 'react';


const Feature = ({ text, icon, iconBg }) => {
    return (
        <Stack direction={'row'} align={'center'}>
            <Flex
                w={8}
                h={8}
                align={'center'}
                justify={'center'}
                rounded={'full'}
                bg={iconBg}>
                {icon}
            </Flex>
            <Text fontWeight={600}>{text}</Text>
        </Stack>
    );
};

export default function About() {
    return (
        <Container maxW={'9xl'} py={12} width={"99.8vw"} color="white" backgroundColor="#191527">
            <Heading>ABOUT ME</Heading>
            <SimpleGrid columns={{ base: 1, md: 2 }} spacing={10}>
                <Stack spacing={4}>
                    <Text fontSize={'lg'}>
                        A passionate web developer who has more than 1000+ hours of practical knowledge in coding and is proficient in  Node JS, React JS, JavaScript, CSS, HTML and GitHub. Looking to join an organization where he can show his skills and contribute to the organization.
                    </Text>
                </Stack>
                <Flex>
                    <Image
                        borderRadius={"full"}
                        alt={'kamal gupta'}
                        width={{base:"100px",md:"200px"}}
                        src={
                            'https://avatars.githubusercontent.com/u/107463708?v=4'
                        }
                    />
                </Flex>
            </SimpleGrid>
        </Container>
    );
}