
import {
  Box,
  Heading,
  Container,
  Text,
  Stack,
  Image,
} from '@chakra-ui/react';
// import Profile from "../images/Profile.webp"
export default function HomeSection() {
  return (    
      <Container  bgColor="#191527" maxW={"9xl"} color={"white"} backgroundColor="#191527">
        <Stack
          as={Box}
          spacing={{ base: 8, md: 14 }}
          py={{ base: 20, md: 36 }}>
          <Image 
            marginLeft={'45vw'}
            borderRadius={"full"}
            boxSize='200px' bor src={"https://avatars.githubusercontent.com/u/107463708?v=4"}
            alt='Kamal Gupta'
          />
          <Heading
            fontWeight={500}
            textAlign="center"
            fontSize={{ base: '2xl', sm: '4xl', md: '6xl' }}
            lineHeight={'110%'}>
            Kamal Gupta <br />
            <Text as={'span'} color={'green.400'} textAlign="center">
              Full Stack Web Developer
            </Text>
          </Heading>
        </Stack>
      </Container>
    
  );
}

